main {
  height: 100%;

  background-color: #f5f5f5;
  background: url('../public/images/background.jpg');
  background-size: 100px 100px;
  background-repeat: repeat;

  font-family: Arial, Helvetica, sans-serif;

  color: white;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

img.frame {
  z-index: 10;
}

svg.frame-shadow {
  z-index: 1;
}

div#root {
  height: 100%;
}

div.information {
  margin: 20px;
  background: #fffff3;

  -webkit-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));

  border-radius: 2px;
}

div.description {
  padding: 15px;
  padding-top: 25px;
  color: #424242;
  background: #f3f5e4;
}

div.no-description-spacer {
  padding: 15px;
  padding-top: 25px;
}

div.name {
  padding: 15px;
  padding-bottom: 25px;
  color: black;
  font-weight: 700;
}

table.hand {
  border: 0px;
  margin: 0px;
  padding: 0px;

  border-spacing: 0px;

  width: 364px;
}

tr {
  border: 0px;
  margin: 0px;
  padding: 0px;
}

td {
  border: 0px;
  margin: 0px;
  padding: 0px;
}

a:link {
  text-decoration: none;
}

a:visited {
  color: #eeeeee;
}

table>img {
  border: 0px solid black;
}